.wrapper {
  display: flex;
  gap: 5px;
}

.wrapper input {
  line-height: 1.5em;
  border-radius: 2px;
  border: 1px solid lightgray;
  font-size: 15px;
  padding: 6px 8px;
}

.wrapper svg {
  margin-inline: -2rem;
}

@media (max-width: 700px) {
  .wrapper {
    display: none;
  }
}
